import React, { useEffect, useState } from 'react';
import Footer from '../components/Footer';
import Header from '../components/Header';
import Input from '../components/Input';
import { Link , useLocation } from 'react-router-dom';
import '../css/styles.css';

function Verification() {
    const [code, setCode] = useState('');
    const [codeComp, setCodeComp] = useState({color:"primary", helper:"Por favor, complete el campo de correo electrnico", isReady:false});
    const [canSignin, setCanSignin] = useState(false);
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    const emailFromQuery = searchParams.get('email');
    const validCodes = ['1234', '5678', '9876', '4321'];

    
    useEffect(()=>{
        
        let isOk = false;
        if (code.length == 0) {
            isOk = true;
            codeComp.isReady = false;
        }
        if (!/^\d+$/.test(code)) {
            codeComp.helper = "Solo se permiten nmeros";
        }
        else {
            if (code.length !== 4) {
                codeComp.helper = "El cdigo debe tener 4 dgitos";
            }
            else if (validCodes.includes(code)){
                isOk = true;
                codeComp.isReady = true;
            }
            else {
                codeComp.helper = "El cdigo debe ser el correcto";
                isOk = false;
                codeComp.isReady = false;
            }
        }
        if (isOk) { codeComp.helper = ""; }
        setCodeComp({...codeComp, color:isOk?'primary':'danger'});
    }, [code]);

    useEffect(()=>{
        setCanSignin(codeComp.isReady);
    },[codeComp.isReady]);

    return (
        <div className='full-page'>
            <Header />
            <main className='w-auto grow min-h-full flex justify-center items-center gap-x-32'>
                <div className="centered-box">
                    <div className="restore-container">
                        <h1 className="restore-title">Introducir tu cdigo de verificacin</h1>
                        <br />
                        <p className="restore-description">
                            Tu cdigo de verificacin ha sido enviado a {emailFromQuery}. Por favor, introduzca el cdigo recibido.
                        </p>
                        <br />
                        <div className="contact-support">
                            <p>
                            Si no recibi el correo electrnico, verifique todos sus correos electrnicos, incluida la carpeta de correo no deseado.
                            </p>
                        </div>
                        <br />
                        <br />
                        <p className="email-label">Cdigo de verificacin</p>
                        <br />
                        <form name='restore' id='restore' className='flex flex-col gap-y-4'>
                    <       Input label="Cdigo de verificacin" helpText={codeComp.helper}
                            type="text" color={codeComp.color} setValue={setCode}/>

                            <Link to="/Confirm">
                                <button className='btn-primary w-full' type='submit' disabled={!canSignin}>
                                    PRXIMO
                                </button>
                            </Link>
                        </form>
                    </div>
                </div>
            </main>
            <Footer />
        </div>
    );
}

export default Verification;